import React from 'react';
import moment from 'moment-timezone';
import { Container } from 'react-bootstrap';

import Avatar from 'components/Avatar';
import TagLabel from 'components/label/Tag';

import './index.scss';

type Props = {
  frontmatter: {
    tags: { color: string; id: string; name: string }[];
    title: string;
    date: string;
    preview?: { external?: { url?: string } }[];
    writer?: { name: string }[];
  };
};

function PostHeader({ frontmatter }: Props) {
  const { title, tags, date, writer } = frontmatter;

  return (
    <div className='post-header'>
      <Container>
        {tags && (
          <div className='post-header__tags'>
            {tags.map((tag, index) => (
              <TagLabel tag={tag} key={`${tag}-${index}`} />
            ))}
          </div>
        )}

        <h1 className='post-header__title'>{title}</h1>

        <div className='post-header__sub'>
          <Avatar name={writer?.[0].name ?? ''} />

          <div>
            <b>{writer?.[0].name}</b>
            <div className='post__date'>
              {/* eslint-disable-next-line newline-per-chained-call */}
              {moment(date).tz('Asia/Seoul').format('YYYY. MM. DD')}
            </div>
          </div>
        </div>
      </Container>
    </div>
  );
}

export default PostHeader;
