import React from 'react';
import { Container } from 'react-bootstrap';
import { graphql } from 'gatsby';

import SEOHelmet from 'components/helmet/SEO';
import Layout from 'components/Layout';
import PostHeader from 'components/post/Header';
import PostContent from 'components/post/Content';
import PostToc from 'components/post/Toc';
import NameCard from 'components/card/NameCard';

import { parseTitleForPath } from 'utils/text-parser';

import './index.scss';

export default function Template({
  data,
}: {
  data: {
    allMarkdownRemark: {
      edges: {
        node: {
          frontmatter: {
            tags: { color: string; id: string; name: string }[];
            title: string;
            date: string;
            preview?: { external?: { url?: string } }[];
            writer?: { name: string }[];
          };
          excerpt: string;
          html: string;
          tableOfContents: string;
        };
      }[];
    };
  };
}) {
  const { frontmatter, html, excerpt, tableOfContents } = data.allMarkdownRemark?.edges?.[0]
    ?.node ?? {
    frontmatter: {},
    html: '',
    excerpt: '',
  };

  return (
    <Layout>
      <SEOHelmet
        title={`QUES | ${frontmatter?.title}`}
        description={excerpt}
        image={
          frontmatter.preview?.[0]?.external?.url ||
          'https://avatars.githubusercontent.com/u/76085248?s=200&v=4'
        }
        url={`https://quesdevteam.github.io/post/${parseTitleForPath(frontmatter.title)}`}
      />

      <article className='article'>
        <header
          className='article-header'
          style={{
            backgroundImage: `url(
            ${frontmatter.preview?.[0]?.external?.url || 'https://dummyimage.com/1024x800/000/fff'}
          )`,
          }}
        >
          <PostHeader frontmatter={frontmatter} />
        </header>

        <Container>
          <div className='article-body'>
            <PostContent html={html} />
            {tableOfContents && <PostToc toc={tableOfContents} />}
          </div>

          <footer className='article-footer'>
            <NameCard name={frontmatter.writer?.[0].name ?? ''} />
          </footer>
        </Container>
      </article>
    </Layout>
  );
}

export const pageQuery = graphql`
  query ($id: String!) {
    allMarkdownRemark(filter: { id: { eq: $id } }) {
      edges {
        node {
          frontmatter {
            title
            date
            tags {
              color
              id
              name
            }
            preview {
              external {
                url
              }
            }
            writer {
              name
            }
          }
          excerpt
          html
          tableOfContents
        }
      }
    }
  }
`;
